// Utility untuk handle paths di development vs production
export const getAssetPath = (relativePath) => {
  // Hapus slash di awal jika ada
  const cleanPath = relativePath.startsWith("/")
    ? relativePath.slice(1)
    : relativePath;

  // Jika di production (GitHub Pages), gunakan base URL dari Vite
  if (import.meta.env.PROD) {
    // Vite akan otomatis menangani base URL
    return import.meta.env.BASE_URL + cleanPath;
  }
  // Jika di development, pakai path normal
  return "/" + cleanPath;
};

// Untuk images
export const getImagePath = (imageName) => {
  return getAssetPath(`images/${imageName}`);
};

// Untuk audio
export const getAudioPath = (audioName) => {
  return getAssetPath(`audio/${audioName}`);
};

// Fallback untuk images
export const getFallbackImage = (index) => {
  const fallbackUrls = [
    "https://images.unsplash.com/photo-1534528741775-53994a69daeb?w=800&h=600&fit=crop&auto=format&q=80",
    "https://images.unsplash.com/photo-1517841905240-472988babdf9?w=800&h=600&fit=crop&auto=format&q=80",
    "https://images.unsplash.com/photo-1524504388940-b1c1722653e1?w=800&h=600&fit=crop&auto=format&q=80",
    "https://images.unsplash.com/photo-1488426862026-3ee34a7d66df?w=800&h=600&fit=crop&auto=format&q=80",
    "https://images.unsplash.com/photo-1544005313-94ddf0286df2?w=800&h=600&fit=crop&auto=format&q=80",
  ];
  return fallbackUrls[index % fallbackUrls.length];
};
