import React, { useEffect, useRef, useState } from "react";
import gsap from "gsap";
import confetti from "canvas-confetti";
import { Howl } from "howler";
import { getImagePath, getAudioPath } from "../utils/paths";

const Landing = ({ setActiveSection }) => {
  const titleRef = useRef(null);
  const buttonRef = useRef(null);
  const photoRefs = useRef([]);
  const [showPhotos, setShowPhotos] = useState(false);
  const [musicPlaying, setMusicPlaying] = useState(false);
  const [audioError, setAudioError] = useState(false);
  const [isAnimating, setIsAnimating] = useState(true);
  const [buttonText, setButtonText] = useState("Klik untuk Kejutan!");
  const soundRef = useRef(null);

  // Photo configuration
  const photos = [
    {
      id: 1,
      name: "imu1.jpeg",
      alt: "Foto Kenangan 1",
      position: { top: "10%", left: "10%" },
    },
    {
      id: 2,
      name: "imu2.jpeg",
      alt: "Foto Kenangan 2",
      position: { top: "70%", left: "80%" },
    },
    {
      id: 3,
      name: "imu3.jpeg",
      alt: "Foto Kenangan 3",
      position: { top: "20%", left: "80%" },
    },
    {
      id: 4,
      name: "imu4.jpeg",
      alt: "Foto Kenangan 4",
      position: { top: "80%", left: "20%" },
    },
  ];

  // **FIXED: Initialize audio dengan musik lokal**
  useEffect(() => {
    console.log(" Initializing audio...");

    try {
      // **PENTING: Gunakan getAudioPath untuk path yang benar**
      const localAudioPath = getAudioPath("birthday-song.mp3");
      console.log(" Audio path:", localAudioPath);

      // Coba sumber audio dalam urutan prioritas:
      const audioSources = [
        localAudioPath, // 1. Musik lokal utama
      ];

      soundRef.current = new Howl({
        src: audioSources,
        volume: 0.5,
        loop: true,
        html5: true,
        preload: true, // Preload audio
        onload: () => {
          console.log(" Audio loaded successfully");
          setAudioError(false);
        },
        onloaderror: (id, error) => {
          console.warn(" Audio load error, trying next source...", error);
          // Howl akan otomatis mencoba sumber berikutnya
        },
        onplayerror: (id, error) => {
          console.error(" Audio play error:", error);
          setAudioError(true);
          setButtonText(" Musik Error");
        },
      });
    } catch (error) {
      console.error(" Audio init error:", error);
      setAudioError(true);
      setButtonText(" Audio Error");
    }

    return () => {
      if (soundRef.current) {
        soundRef.current.stop();
        soundRef.current.unload();
      }
    };
  }, []);

  // GSAP Animations
  useEffect(() => {
    if (!titleRef.current || !buttonRef.current) return;

    const timeline = gsap.timeline();

    try {
      timeline.from(".title-letter", {
        opacity: 0,
        y: 30,
        duration: 0.5,
        stagger: 0.05,
        ease: "power2.out",
      });

      timeline.from(
        buttonRef.current,
        {
          opacity: 0,
          y: 30,
          scale: 0.8,
          duration: 0.8,
          ease: "back.out(1.7)",
        },
        "-=0.3"
      );

      timeline.from(
        ".subtitle",
        {
          opacity: 0,
          y: 20,
          duration: 0.6,
        },
        "-=0.5"
      );

      //  pastikan button aktif setelah animasi selesai
      timeline.eventCallback("onComplete", () => {
        setIsAnimating(false);
      });
    } catch (error) {
      console.error(" GSAP animation error:", error);
      setIsAnimating(false);
    }

    return () => {
      timeline.kill();
    };
  }, []);


  const handleSurpriseClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    console.log(" Surprise button clicked!");

    try {
      // Confetti effect
      confetti({
        particleCount: 80,
        spread: 60,
        origin: { y: 0.6 },
      });

      // **FIXED: Play/pause music dengan error handling**
      if (soundRef.current) {
        if (musicPlaying) {
          soundRef.current.pause();
          setMusicPlaying(false);
          setButtonText(" Putar Musik");
        } else {
          // Coba play musik
          const playResult = soundRef.current.play();

          if (playResult) {
            // Jika berhasil
            setMusicPlaying(true);
            setButtonText(" Pause Musik");
            setAudioError(false);
          } else {
            // Jika gagal
            console.error(" Cannot play audio");
            setAudioError(true);
            setButtonText(" Gagal Memutar");
          }
        }
      } else {
        console.error(" Audio not initialized");
        setAudioError(true);
        setButtonText(" Audio Tidak Ada");
      }

      // Show photos with animation
      if (!showPhotos) {
        setShowPhotos(true);

        // Animate photos in
        setTimeout(() => {
          gsap.fromTo(
            ".photo-item",
            { opacity: 0, scale: 0.5 },
            {
              opacity: 1,
              scale: 1,
              duration: 0.8,
              stagger: 0.2,
              ease: "back.out(1.7)",
            }
          );
        }, 100);
      }

      // Button bounce animation
      gsap.to(buttonRef.current, {
        scale: 0.95,
        duration: 0.1,
        yoyo: true,
        repeat: 1,
        onComplete: () => {
          gsap.to(buttonRef.current, { scale: 1, duration: 0.1 });
        },
      });
    } catch (error) {
      console.error(" Error in handleSurpriseClick:", error);
      setButtonText(" Error, Coba Lagi");
    }
  };

  const title = "Selamat Ulang Tahun Dewi Saskia Juanita";
  const letters = title.split("");

  return (
    <div className="min-h-[80vh] flex flex-col items-center justify-center relative overflow-hidden bg-gradient-to-br from-pink-50 via-yellow-50 to-purple-50 p-4">
      {/* Background Decorations */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(15)].map((_, i) => (
          <div
            key={i}
            className="absolute text-2xl md:text-3xl animate-float"
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animationDelay: `${i * 0.5}s`,
              opacity: 0.2,
            }}
          >
            {["", "", "", "", ""][i % 5]}
          </div>
        ))}
      </div>

      {/* Main Content */}
      <div className="relative z-10 text-center max-w-4xl mx-auto w-full">
        {/* Title */}
        <h1 className="text-3xl md:text-5xl lg:text-6xl font-bold mb-6 md:mb-8">
          {letters.map((letter, index) => (
            <span
              key={index}
              className={`title-letter inline-block ${
                letter === " " ? "mx-1 md:mx-2" : ""
              } ${
                ["", "", "", ""].includes(letter)
                  ? "text-3xl md:text-5xl"
                  : "bg-gradient-to-r from-pink-500 to-purple-500 bg-clip-text text-transparent"
              }`}
            >
              {letter}
            </span>
          ))}
        </h1>

        {/* Subtitle */}
        <p className="subtitle text-lg md:text-xl text-gray-600 mb-8 md:mb-12">
          Hari ini adalah hari spesialmu! 
          <br className="md:hidden" />
          <span className="hidden md:inline"> </span>
          Semoga semua impianmu terkabul! 
        </p>

        {/* Surprise Button */}
        <div className="mb-8 md:mb-12">
          <button
            ref={buttonRef}
            onClick={handleSurpriseClick}
            className="group relative px-6 md:px-8 py-3 md:py-4 bg-gradient-to-r from-pink-500 to-purple-500 text-white text-lg md:text-xl font-bold rounded-full shadow-xl hover:shadow-2xl transition-all duration-300 hover:scale-105 active:scale-95 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {/* Button glow effect */}
            <div className="absolute -inset-3 md:-inset-4 rounded-full border-2 border-pink-300 animate-ping opacity-0 group-hover:opacity-70 transition-opacity"></div>

            {/* Button content */}
            <div className="relative z-10 flex items-center justify-center space-x-2 md:space-x-3">
              <span className="text-xl md:text-2xl">
                {musicPlaying ? "" : ""}
              </span>
              <span>{buttonText}</span>
              <span className="text-xl md:text-2xl"></span>
            </div>
          </button>

          {/* Audio status */}
          {audioError ? (
            <p className="mt-3 text-sm text-yellow-600 animate-pulse">
               Musik offline tidak ditemukan, menggunakan musik online
            </p>
          ) : musicPlaying ? (
            <p className="mt-3 text-sm text-green-600">
               Musik lokal sedang diputar
            </p>
          ) : (
            <p className="mt-3 text-sm text-gray-500">
               Klik untuk konfeti dan musik!
            </p>
          )}
        </div>

        {/* Gallery Navigation Button */}
        <button
          onClick={() => setActiveSection && setActiveSection("gallery")}
          className="inline-flex items-center space-x-2 text-pink-600 hover:text-pink-700 font-medium transition-all hover:scale-105 px-4 py-2 rounded-lg hover:bg-pink-50"
        >
          <span className="text-xl"></span>
          <span>Lihat Galeri Foto Kenangan Kita</span>
          <span className="text-xl"></span>
        </button>
      </div>

      {/* Floating Photos */}
      {showPhotos && (
        <div className="absolute inset-0 pointer-events-none z-0">
          {photos.map((photo, index) => (
            <div
              key={photo.id}
              className="photo-item absolute w-16 h-16 md:w-24 md:h-24 rounded-full overflow-hidden border-4 border-white shadow-xl"
              style={photo.position}
            >
              <img
                src={getImagePath(photo.name)}
                alt={photo.alt}
                className="w-full h-full object-cover"
                onError={(e) => {
                  console.log(` Error loading: ${photo.name}`);
                  e.target.src = `https://images.unsplash.com/photo-${
                    150 + index
                  }?w=400&h=400&fit=crop&auto=format`;
                  e.target.onerror = null;
                }}
                loading="lazy"
              />
            </div>
          ))}
        </div>
      )}

      {/* Debug Info - Hapus di production */}
      {process.env.NODE_ENV === "development" && (
        <div className="fixed bottom-4 left-4 bg-black/80 text-white text-xs p-2 rounded opacity-50">
          <div>Button Text: {buttonText}</div>
          <div>Music: {musicPlaying ? "Playing" : "Stopped"}</div>
          <div>Audio Error: {audioError.toString()}</div>
        </div>
      )}
    </div>
  );
};

export default Landing;
